import { ReactElement } from "react"
import { LazyImage } from "@rp/react/web/common/base-components"
import { Box, Skeleton } from "@rp/react/web/mui/core"

type Props = {
  isLoading?: boolean
  src?: string
  alt?: string
  fallback?: ReactElement
}

const Loading = () => <Skeleton height="100%" width="100%" sx={{ transform: "scale(1)" }} />

export const WatchlistCardImage = ({ isLoading, src, alt, fallback }: Props) => {
  return (
    <Box sx={{ width: "100%", height: 95, "& img": { objectFit: "cover", borderRadius: "8px" } }}>
      <LazyImage
        loading={isLoading}
        width={"100%"}
        height={95}
        src={src}
        alt={alt}
        loadingFallback={<Loading />}
        fallback={fallback}
      />
    </Box>
  )
}
